import { typeList, chainList } from './comStatic'

/**
 * 时间转换
 * @param {String} data   //毫秒时间戳
 * @param {Number} type   //展示方式
 */
 const _formatDate = (data, type) => {
	var date = new Date(+data)
	var y = date.getFullYear()
	var m = date.getMonth() + 1
	m = m < 10 ? ('0' + m) : m
	var d = date.getDate()
	d = d < 10 ? ('0' + d) : d
	var h = date.getHours()
	h = h < 10 ? ('0' + h) : h
	var minute = date.getMinutes()
	minute = minute < 10 ? ('0' + minute) : minute
	var second = date.getSeconds()
	second = second < 10 ? ('0' + second) : second
	if (type === 1) {
	  	return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second
	} else if (type === 2) {
	  	return y + '-' + m + '-' + d + ' ' + h + ':' + minute
	} else if (type === 3) {
	  	return m + '-' + d + ' ' + h + ':' + minute + ':' + second
	}else if (type === 4) {
		return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second
	}else if (type === 5) {
		return m + '月' + d + '日'
	}else if (type === 6) {
		return y + '/' + m + '/' + d 
	}else if (type === 7) {
		return y + '/' + m + '/' + d + ' ' + h + ':' + minute 
	}else if (type === 8) {
		return  m + '/' + d + ' ' + h + ':' + minute
	}else if (type === 9) {
		return h + ':' + minute + ' ' + m + '/' + y
	}else if (type === 10) {
		return y + '-' + m + '-' + d 
	}else if (type === 11) {
		return m + '-' + d 
	}else if (type === 12) {
		return m + '/' + d + ' ' + h + ':' + minute
	}else {
	  return h + ':' + minute + ':' + second
	}
}

/** 
 * 格式化数字，返回小数点后不为0的2位
*/
const _formatNum = (num, accuracy, status) => {
	const size = accuracy ? accuracy : 2
	if(num) {
		if (String(Number(num)).indexOf('.')>-1) {
			const len = num.toString().match(/(\.0*)/)[0].length - 1
			return Number(Number(num).toFixed(len+size))
		} else {	
			if(status) {
				return Number(num)
			} else {
				return Number(num).toFixed(size)
			}
		}
	} else {
		return '--'
	}
}

/**
 * 截取2位数据
 * @param {Number/String} num //待处理数据
 * @param {Number} accuracy   //精度
 * @param {Bool} status 	  //是否保持原数据
 */
 const _sliceData = (num, accuracy, status) => {
	if(num) {
		const size = accuracy ? accuracy : 2
		const arr = num.toString().split('.')
		if (arr.length > 1) {
			if (arr[1].length >= size) {
				let data = `${arr[0]}.${arr[1].slice(0, size)}`
				if(status) data = Number(data)
				return data
			} else {
				let lastData = arr[1]
				if(!status) {
					for(let i=0; i < size - arr[1].length; i++){
						lastData += '0'
					}
					return `${arr[0]}.${lastData}`
				} else {
					return Number(`${arr[0]}.${lastData}`)
				}
			}
		} else {
			if(!status) {
				return `${num}.00`
			} else {
				return Number(num)
			}
		}
	} else {
		return '0.00'
	}
}

/**
 * 获取图标地址
 * @param {String} chain  //BSC,HECO,ETH链名称
 * @param {String} addr   //图标hash地址
 * @param {Number} type   //投注类型
 */

const _coinAddress = (chain, addr, type) => {
	return `https://mdex.com/token-icons/${chainList[chain]}/${typeList[type]}/${addr}.png?v=${Math.random()*1000}`
}

export {
  _formatDate,
  _formatNum,
  _sliceData,
  _coinAddress
}
