import React from 'react'
import { observer, inject } from 'mobx-react'
import styles from './PCFooter.module.scss'
import hmdx from './img/hmdx.png'
import github from './img/github.png'
import medium from './img/medium.png'
import telegram from './img/telegram.png'
import twitter from './img/twitter.png'
import discord from './img/discord.png'

const PCFooter = ({ store })=> {

  const { lang, skin } = store

  // 语言
  const la = lang['language']

  // 智能链
  const sk = skin.skinSign

  const footerTextObj = lang[la]['Footer']

  
  // 设置语言参数
  const setlangPram = (val) => {
    return `?lang=${val === 'en' ? 'en' : 'zh-CN'}`
  }
  
  const mdexUrl = `https://mdex.com/#${setlangPram(la)}/`

  const footerHrefObj = {
    Application: {
      ACText1: 'https://docs.google.com/forms/d/e/1FAIpQLSem595jWpO5_A5fRj9W4-wX_3mg3ypqqNiXcC_yi-tpcbOaqA/viewform',
      ACText2: 'https://docs.google.com/forms/d/e/1FAIpQLSem595jWpO5_A5fRj9W4-wX_3mg3ypqqNiXcC_yi-tpcbOaqA/viewform',
      ACText3: 'https://forms.gle/b6SCc4cXzn7apFhk7',
    },
    Guide: {
      GUText1: `https://${sk === 'BSC' ? 'bsc' : 'ht'}.mdex.com/#/swap${setlangPram(la)}`,
      GUText2: `https://${sk === 'BSC' ? 'bsc' : 'ht'}.mdex.com/#/pool${setlangPram(la)}`,
      GUText3: `${sk === 'BSC' ? 'https://bsc-info.mdex.com/' : 'https://info.mdex.com/'}`,
      GUText4: `https://mdex.com/#/liquidity${setlangPram(la)}`,
      GUText5: `https://mdex.com/#/boardroom${setlangPram(la)}`,
      GUText6: `https://mdex.com/#/trading${setlangPram(la)}`,
    },
    Doc: {
      DOText1: 'https://mdexcom.zendesk.com/hc/zh-cn/sections/360013068171-FAQ',
      DOText2: '#',
    },
    About: {
      ABText1: 'https://mdexcom.zendesk.com/hc/en-gb/sections/360012110652-Announcement',
      ABText2: 'https://mdexcom.zendesk.com/hc/en-gb/sections/360012110672-Adjustment-of-Pool',
      ABText3: '#',
      ABText4: '#'
    }
  }

  // 导航条渲染
  const footerRender = (name, data) => {
    let list = []
    list.push(<li key={`footerRender${name}`}>{footerTextObj[name]}</li>)
    Object.keys(data).forEach((item) => {
      list.push( 
        <li key={`footerRender${item}`}>
          {
            data[item] !== '#' ?  (<a target='_blank' rel ='noreferrer' href={data[item]}>{footerTextObj[item]}</a>) : footerTextObj[item]
          }
        </li>
      )
    })
    return list
  }

  // 联系方式渲染
  const contactRender = () => {
    const contactList = {
      github: {url: 'https://github.com/mdexSwap', address: github},
      medium: {url: 'https://mdex.medium.com/', address: medium},
      telegram: {url: `https://t.me/${la === 'en' ? 'MdexEN' : 'MixDex'}`, address: telegram},
      twitter: {url: 'https://twitter.com/Mdextech', address: twitter},
      discord: {url: 'https://discord.com/invite/3TYDPktjqC', address: discord}
    } 
    let list = []
    Object.keys(contactList).forEach((item, index) => {
      list.push(
        <a target='_blank' rel ='noreferrer' href={contactList[item]['url']} className={`${styles[item]}`} key={`contactRender${index}`}>
          <img src={contactList[item]['address']} alt=''/>
        </a>
      )
    })
    return list
  }

  return (
    <div className={styles.footer}>
      <div className={styles.left}>
        <div className={styles.logo}>
          <a href={mdexUrl}><img src={hmdx} alt=''/></a>
        </div>
        <div className={styles.contact}>{contactRender()}</div>
        <p>{footerTextObj['Reserved']}</p>
      </div>
      <div className={styles.right}>
        <ul>{footerRender('Application', footerHrefObj['Application'])}</ul>
        <ul>{footerRender('Guide', footerHrefObj['Guide'])}</ul>
        <ul>{footerRender('Doc', footerHrefObj['Doc'])}</ul>
        <ul>{footerRender('About', footerHrefObj['About'])}</ul>
      </div>
    </div>
  )
}

export default inject('store')(observer(PCFooter))
