import React, { useCallback } from 'react'
import { Select } from 'antd'
import { observer, inject } from 'mobx-react'
import { connect, changeChain } from '@/assets/utils/publicErc20'
import styles from './PCHeader.module.scss'
import logo from './img/logo.png'

const { Option } = Select

const PCHeader = ({ store })=> {

  const { lang, skin, contract } = store

  // 语言
  const la = lang['language']

  // 智能链
  const sk = skin.skinSign

  const navTextObj = lang[la]['Header']

  // 设置语言参数
  const setlangPram = (val) => {
    return `?lang=${val === 'en' ? 'en' : 'zh-CN'}`
  }
  
  connect().then(accounts => {
    console.log('accounts', accounts[0])
    contract.setAccounts(accounts[0])
  }) 
  const connectWallet = useCallback(async() => {
    console.log('connectWallet')
    connect().then(accounts => {
      console.log('accounts', accounts[0])
      contract.setAccounts(accounts[0])
    }) 
  }, [])

  

  const navHrefObj = {
    'Home': `https://mdex.com/#/${setlangPram(la)}`,
    'Swap': `https://${sk === 'BSC' ? 'bsc' : 'ht'}.mdex.com/#/swap${setlangPram(la)}`,
    'Pool': `https://${sk === 'BSC' ? 'bsc' : 'ht'}.mdex.com/#/pool${setlangPram(la)}`,
    'Liquidity': `https://mdex.com/#/liquidity${setlangPram(la)}`,
    'Trading': `https://mdex.com/#/trading${setlangPram(la)}`,
    'Boardroom': `https://mdex.com/#/boardroom${setlangPram(la)}`,
    'Bridge': `https://mdex.com/#/bridge${setlangPram(la)}`,
    'Chart': `${sk === 'BSC' ? 'https://bsc-info.mdex.com/' : 'https://info.mdex.com/'}`,
    'News': `https://mdexcom.zendesk.com/hc/${la === 'en' ? 'en-gb' : 'zh-cn'}`,
    'IMO': '#',
  }

  // 导航条渲染
  const navRender = () => {
    let list = []
    Object.keys(navHrefObj).forEach((item, index) => {
      list.push( <a className={item === 'IMO' ? styles.active : ''} href={navHrefObj[item]} key={`navRender${index}`}>{navTextObj[item]}</a>)
    })
    return list
  }

  // 皮肤更改
  const skinHandleChange = async(value) => {
    // skin.setSkinSign(tranferChainList[value])
    let config = {}
    if(value == 56){
      config= {
       chainId: '0x38',
       chainName: 'BSC Mainnet',
       name: 'BSC',
       symbol: 'BNB',
       rpcUrls: ['https://bsc-dataseed.binance.org'],
       blockExplorerUrls: ['https://bscscan.com/']
     }
   }else{
     config={
       chainId: '0x80',
       chainName: 'Heco Mainnet',
       name: 'Heco',
       symbol: 'HT',
       rpcUrls: ['https://http-mainnet-node.huobichain.com'],
       blockExplorerUrls:['https://hecoinfo.com']
     }
   }
    await changeChain(config)
  }

  // 语言更改
  const lanHandleChange =(index) => { 
    lang.setLang(index)
  }

  return (
    <div className={`${styles.header} ${styles[sk]}`}>
      <div className={styles.logo} >
        <a href={navHrefObj['Home']}><img src={logo} alt='' /></a>
      </div>
      <div className={styles.nav}>{navRender()}</div>
      <div className={styles.right}>
        <Select value={skin.skinSign} className={styles.skin} onChange={skinHandleChange}>
          <Option value="56">BSC</Option>
          <Option value="128">HECO</Option>
        </Select>
        {
          !contract.accounts ? (<div className={styles.wallet} onClick={connectWallet}>{navTextObj['Connect']}</div>) : <React.Fragment></React.Fragment>
        }
        <Select defaultValue={la} className={styles.language} onChange={lanHandleChange}>
          <Option value="en">English</Option>
          <Option value="zh">简体中文</Option>
        </Select>
      </div>
    </div>
  )
}

export default inject('store')(observer(PCHeader))
